<template>
    <div class="element"  :style="[{'background-color': elementColor(element.serie) }]	">
        <output>
            <b>{{ element.atomicNumber  }}</b>
            <abbr>{{ element.symbol  }}</abbr>
            <span > ? </span>
            <data>{{ element.atomicMass  }}</data>
        </output>
        <small v-if="element.energyLevels && element.energyLevels.length > 0">
            <span v-for="(item, index) in completeEnergyLevels(element.energyLevels)" :key="index">
                {{item}}
            </span>
        </small>
    </div>
</template>
  
  <script>
  export default {
    name: 'ElementCard',
    props: {
      element: Object
    },
    methods: {
			completeEnergyLevels(levels) {
				const maxLevels = 7;
				let completedLevels = levels;

				while (completedLevels.length < maxLevels) {
						completedLevels.push(' ');
				}

				return completedLevels;
			},
			elementColor(serie){
				if(serie == "Não metais"){
					return "#8CED8C";
				} else if(serie == "Gás nobre"){
					return "#E5BDE5"
				} else if(serie == "Metal alcalino"){
					return "#EACE5D"
				} else if(serie == "Metal alcalino-terroso"){
					return "#F1F165"
				}  else if(serie == "Metalóide"){
					return "#9EE5D4"
				} else if(serie == "Halogênio"){
					return "#70CBEB"
				}else if(serie == "Metal pobre"){
					return "#ACDFEC"
				}else if(serie == "Metal de transição"){
					return "#FAC5B7"
				} else if(serie == "Desconhecido"){
					return "#EEEEEE"
				}else if(serie == "Actinídeo"){
					return "#F5CCDA"
				}else if(serie == "Lantanídeo"){
					return "#E8D19C"
				}
			}
    }
  };
  </script>
<style>

.element {
    width: 130px;
    height: 130px;
    padding: 0.75rem;
    font-size: 1em;
    display: flex;
		border-radius: 5px;
    flex-direction: row;
    justify-content: space-between;
    min-width: 0;
    white-space: nowrap;
    box-shadow: 0 1px 2px rgb(0 0 0 / 50%);
}
.element b{
    text-align: left;
}
.element > small {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    line-height: 1;
}
.element data {
	font-size: 0.8rem;
}

output {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	line-height: 1;
	text-align: left;
}
output > span {
	
	font-size: 1.5rem;
}
abbr{
    font-size: 4rem;
}
small {
  font-size: 0.75em;
  text-align: end;
}

@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

.element {
  transition: transform 0.3s ease-in-out;
}

.element.shake {
  animation: shake 0.2s;
  animation-timing-function: ease-in-out;
}

.word {
  transition: transform 0.3s ease-in-out;
}

.word.shake {
  animation: shake 0.2s;
  animation-timing-function: ease-in-out;
}

</style>
  