<template>
  <div class="game">
    <h3 style="color: #fff; padding-bottom: 20px;">Qual nome deste elemento químico?</h3>
    <ElementCard :element="currentElement" />
    <p v-if="feedback">{{ feedback }}</p>
    
    <transition name="fade">
      <div :class="[{'fade-leave-active': !showAlert }, 'alert', 'warning', ]">
        {{ msgAlert }}
      </div>
    </transition>
    <div class="word" v-if="(wordNormalized.length > 0)">
      <div v-for="(letter, index) in wordArray" :key="index" class="block">
        <div :key="index"
        :class="['div-letter',  { 'letter-confirmed': guessedLetters.includes(letter) ? letter : '', 'letter-selected': index === selectedIndex }]"
        @click="selectDiv(index)"
        ><span>{{ typedLetters[index]  }} </span></div>
      </div>
    </div>    
    <Keyboard :markUsed="markUsed" :listWrongLetters="wrongLetters" :listGuessedLetters="guessedLetters"/>
  </div>
</template>

<script>
import ElementCard from './ElementCard.vue';
import Keyboard from './Keyboard.vue';
import axios from "axios";

export default {
  name: 'GameComponent',
  components: { ElementCard, Keyboard },
  data() {
    return {
      currentElement: {},
      userGuess: '',
      feedback: '',
      wordNormalized: '',

      usedButtons: [],
      guessedLetters: [],
      wrongLetters: [],
      someCondition: false,

      typedLetters: [],
      selectedIndex: 0,
      clickedIndex: null,
      msgAlert: '',
      showAlert: false
    };
  },
  created() {
    this.pickRandomElement();
  },
  computed: {
    wordArray() {
      return this.wordNormalized.split('');
    },
  },
  methods: {
    selectDiv(index) {
      if (this.guessedLetters[index]) {
        return; 
      }
      this.selectedIndex = index;
      this.clickedIndex = index; 
    },
    markUsed(letter) {

      this.showAlert = false;

      switch (letter) {
        case '←':
          this.handleBackspace();
          break;
        case 'ENTER':
          if(this.countLettersArray(this.typedLetters) >= this.wordNormalized.length){
            this.handleEnter();
          } else {
            console.log(this.typedLetters.length, this.wordNormalized);

            this.showAlert = true;
            this.msgAlert = "A palavra tem que conter "+this.wordNormalized.length+" letras.";
            this.triggerShakeWord();
          }
          break;
        default:
          this.handleLetterInput(letter);
      }
    },

    handleBackspace() {
      if (this.clickedIndex != null) {
        this.typedLetters[this.selectedIndex] = '';
      } else {
        if (this.selectedIndex > 0) {
          this.selectedIndex--;
        }
        this.typedLetters[this.selectedIndex] = '';
      }
      console.log("Apagou: ", this.selectedIndex, this.wordNormalized.length, this.typedLetters[this.selectedIndex]);
    },

    handleEnter() {
      for (let i = 0; i < this.wordNormalized.length; i++) {
        if (this.wordNormalized[i] !== this.typedLetters[i]) {
          this.typedLetters[i] = "";
          if (!this.wrongLetters.includes(this.typedLetters[i])) {
            this.wrongLetters.push(this.typedLetters[i]);
          }
        } else {
          this.guessedLetters[i] = this.wordNormalized[i];
        }
      }

      this.positionSelectIndex();

      if (this.compareArrayToString(this.guessedLetters, this.wordNormalized)) {
        console.log("Parabéns");
      } else {
        this.triggerShake();
      }

      console.log(this.typedLetters);
    },
    positionSelectIndex() {
      const emptyIndex = this.typedLetters.findIndex(letter => letter === "");
      if (emptyIndex !== -1) {
        this.selectedIndex = emptyIndex;
        return;
      }
    },
    countLettersArray(arr) {
      let count = 0;
      for (const element of arr) {
        if (element && typeof element === 'string' && element.trim() !== '') {
          count++;
        }
      }
      return count;
    },
    handleLetterInput(letter) {

      console.log(this.typedLetters);

      // Verifica se já foram preenchidas todas as letras disponíveis
      if (this.selectedIndex >= this.wordNormalized.length) {
        return; // Sai do método se todas as letras já estiverem preenchidas
      }

      if(this.clickedIndex != null ){
        this.typedLetters[this.clickedIndex] = letter;
        this.clickedIndex = null;
        return;        
      }

      // Atualiza o índice selecionado para a próxima posição vazia
      for (let i = 0; i < this.typedLetters.length; i++) {
        if (this.typedLetters[i] === "") {
          this.selectedIndex = i; 
          break; 
        }
      }

      // Adiciona a letra apenas se ainda houver espaço disponível
      if (this.selectedIndex < this.wordNormalized.length) {
        this.typedLetters[this.selectedIndex] = letter;
        this.selectedIndex++;
      }

      
    },

    compareArrayToString(array, string) {
      // Converte o array em uma string concatenando todos os elementos
      let arrayAsString = array.join('');
      // Compara a string resultante com a string fornecida
      return arrayAsString === string;
    },
    accentWorkRemove(word) {
        return word.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    },
    async pickRandomElement() {
      var elements = await this.listElements();
      const randomIndex = Math.floor(Math.random() * elements.length);
      this.currentElement = elements[randomIndex];
      console.log(this.currentElement);
      this.wordNormalized = this.accentWorkRemove(this.currentElement.name.toUpperCase());
      this.typedLetters = new Array(this.wordNormalized.length);
      this.guessedLetters = new Array(this.wordNormalized.length);
    },
    checkGuess() {
      if (this.userGuess.toLowerCase() === this.currentElement.name.toLowerCase()) {
        this.feedback = 'Correto!';
        this.pickRandomElement();
      } else {
        this.feedback = 'Tente novamente!';
        this.triggerShake();
      }
      this.userGuess = '';
    },
    async listElements() {
      try {
          // Utilize a URL completa para a requisição GET
          const response = await axios.get(`elements.json`); // Assumindo que 'words' é o caminho correto para os arquivos

          let data = response.data;
          console.log(data);
          return data.elements;
      } catch (error) {
          console.error('Erro ao buscar palavras:', error);
          return 'Erro ao buscar palavras.';
      }
    },
    triggerShake() {
      const element = this.$el.querySelector('.element');
      element.classList.add('shake');
      setTimeout(() => {
        element.classList.remove('shake');
      }, 500); // Duração da animação de shake (0.5s)
    },
    triggerShakeWord() {
      const element = this.$el.querySelector('.word');
      element.classList.add('shake');
      setTimeout(() => {
        element.classList.remove('shake');
      }, 500); // Duração da animação de shake (0.5s)
    }    
  }
};
</script>
